.hed{
    border: 2px solid red;
    background-color: bisque;
    width: 1408px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.hed > ul >li{
    font-size: 30px;
    color: red;
}
.hed > h1{
    font-size: 40px;
    color: red;
}
.hed >img {
    width: 80px;
    height: 75px;
    border-radius: 50%;
}